<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><i class="el-icon-lx-calendar" /> </el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="plugins-tips">
        Vue-Quill-EditorQuillVue2
        <a href="https://github.com/surmon-china/vue-quill-editor" target="_blank">vue-quill-editor</a>
      </div>
      <quill-editor ref="myTextEditor" v-model="content" :options="editorOption" />
      <el-button class="editor-btn" type="primary" @click="submit"></el-button>
    </div>
  </div>
</template>

<script>
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'
export default {
  name: 'Editor',
  components: {
    quillEditor
  },
  data: function() {
    return {
      content: '',
      editorOption: {
        placeholder: 'Hello World'
      }
    }
  },
  methods: {
    onEditorChange({ editor, html, text }) {
      this.content = html
    },
    submit() {
      console.log(this.content)
      this.$message.success('')
    }
  }
}
</script>
<style scoped>
    .editor-btn{
        margin-top: 20px;
    }
</style>
